<template>
  <div>
    <input
      type="text"
      v-model="newTodo"
      @keyup.enter="addTodo"
      placeholder="What needs to be done?"
    />
    <ul>
      <li v-for="todo in activeTodos" :key="todo.id">
        <input type="checkbox" v-model="todo.completed" />
        <span v-if="!todo.editing">{{ todo.text }}</span>
        <input
          type="text"
          v-else
          v-model="todo.text"
          @blur="todo.editing = false"
          @keyup.enter="todo.editing = false"
        />
        <button
          @click="editTodo(todo)"
          v-if="!todo.editing"
          class="edit-button"
        >
          Edit
        </button>
        <button @click="deleteTodo(todo.id)" class="delete-button">
          Delete
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
// import CompletedTodos from "./components/CompletedTodos.vue";
export default {
  name: "HomeVue",
  components: {
    // CompletedTodos,
  },
  data() {
    return {
      newTodo: "",
      todos: JSON.parse(localStorage.getItem("todos")) || [],
    };
  },
  watch: {
    todos: {
      handler(todos) {
        localStorage.setItem("todos", JSON.stringify(todos));
      },
      deep: true,
    },
  },
  methods: {
    addTodo() {
      if (this.newTodo.trim() === "") return;
      this.todos.push({
        id: Date.now(),
        text: this.newTodo,
        completed: false,
        editing: false,
      });
      this.newTodo = "";
    },
    deleteTodo(id) {
      this.todos = this.todos.filter((todo) => todo.id !== id);
    },
    editTodo(todo) {
      todo.editing = true;
    },
  },
  computed: {
    activeTodos() {
      return this.todos.filter((todo) => !todo.completed);
    },
  },
};
</script>
<style>
body {
  font-family: Arial, sans-serif;
  margin: 0%;
  padding: 0%;
  background-image: url("../assets/_bc5382ec-1669-48a3-8697-6c595f94b79d.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
}

#app {
  max-width: 600px;
  margin: 40px auto;
}

h1 {
  text-align: center;
}

input[type="text"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #ccc;
  outline: none;
  /* border: none; */
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* li {
  display: flex;
  align-items: center;
} */

li + li {
  margin-top: 10px;
}

input[type="checkbox"] {
  margin-right: 10px;
}

button {
  border-radius: 5px;
  margin-left: auto;
}

.delete-button {
  color: white;
  background-color: red;
  border: none;
  padding: 5px 10px;
}

.edit-button {
  color: white;
  background-color: green;
  border: none;
  padding: 5px 10px;
}

button:hover {
  cursor: pointer;
  background-color: black;
  color: white;
}
</style>
