<template>
  <h2>Completed Todos</h2>
  <ul>
    <li v-for="todo in completedTodos" :key="todo.id">
      {{ todo.text }}
      <button @click="uncompleteTodo(todo)" class="edit-button">
        Uncomplete
      </button>
      <button @click="deleteTodo(todo.id)" class="delete-button">Delete</button>
    </li>
  </ul>
</template>

<script>
export default {
  name: "CompletedTodos",
  data() {
    return {
      todos: JSON.parse(localStorage.getItem("todos")) || [],
    };
  },
  computed: {
    completedTodos() {
      return this.todos.filter((todo) => todo.completed);
    },
  },
  watch: {
    todos: {
      handler(todos) {
        localStorage.setItem("todos", JSON.stringify(todos));
      },
      deep: true,
    },
  },
  methods: {
    uncompleteTodo(todo) {
      todo.completed = false;
    },
    deleteTodo(id) {
      this.todos = this.todos.filter((todo) => todo.id !== id);
    },
  },
};
</script>
<style>
* {
  font-family: sans-serif;
  font-size: 20px;
  margin-top: 20px;
}

h2 {
  text-align: center;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  display: flex;
  align-items: center;
}

li + li {
  margin-top: 10px;
}

button {
  margin-left: auto;
}
</style>
